import { createSlice } from '@reduxjs/toolkit';


const dataUserSlice = createSlice({
    name:'dataUser',
    initialState:{
        NFTs: [],
        errorMsg: ""
    },
    reducers: {

        getData: (state, action)=>{
            state.NFTs = action.payload.NFTs
        }
    }
})

const {getData} = dataUserSlice.actions


export const fetching = async (_account, dispatch, _contrato)=>{
            let arrayMetada= new Array()
                
                arrayMetada = []
                let nftBalance = await _contrato.balanceOf(_account)
                let nftBalanceConverted = parseInt(nftBalance)  
                for(let i =0; i < nftBalanceConverted; i++){
                
                    let tokenIdConverted = i
                    let tokenConverted = tokenIdConverted + 1
                    let tokenMetadataURI = await _contrato.tokenURI(tokenConverted)
        
                    if(tokenMetadataURI.startsWith('ipfs://')){
                        
                        tokenMetadataURI = `https://ipfs.io/ipfs/${tokenMetadataURI.split("ipfs://")[1]}`
                    }         
                const tokenMetada = await fetch(tokenMetadataURI).then((resp)=>resp.json())
                arrayMetada.push(tokenMetada)
                }

                dispatch(getData({
                    NFTs: arrayMetada
                }))
        

}

export default dataUserSlice.reducer